import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useHistory, useParams } from "react-router-dom";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Media,
  Row,
} from "reactstrap";
import validateOptions from "../../../../../constants/validate";
import { update } from "../store/action";

const DocumentTab = ({ selected }) => {
  const { register, errors, handleSubmit } = useForm();
  const [discountData, setDiscountData] = useState(null);
  const lang = useSelector((state) => state.common.language);
  const stores = useSelector((state) => state.products);
  const store = useSelector((state) => state.discounts);
  const PriceOptions = validateOptions.Document_Link_Options;
  const dispatch = useDispatch();
  const history = useHistory();
  const [disable, setDisable] = useState(false);
  const { id } = useParams();
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    if (
      selected !== null ||
      (selected !== null &&
        discountData !== null &&
        selected.id !== discountData.id)
    ) {
      setDiscountData(selected);
    }
  }, [selected]);

  const onSubmit = (values) => {
    if (isObjEmpty(errors)) {
      setDisable(true);
      dispatch(
        update({
          id: parseInt(id),
          data: {
            lang,
            link_document_api: values.link_document_api,
            product_id: parseInt(selected.product_id),
          },
        })
      );
    }
  };

  return (
    <Row>
      <Col sm="12">
        <Media className="mb-2">
          <Media className="mt-50" body>
            <h4>{selected.username} </h4>
          </Media>
        </Media>
      </Col>
      <Col sm="12">
        <Form onSubmit={handleSubmit(onSubmit)}>
          <FormGroup>
            <Label for="link_document_api">
              <FormattedMessage id="DOCUMENT LINK API" />{" "}
              <span className="text-danger">*</span>
            </Label>
            <Input
              name="link_document_api"
              id="link_document_api"
              placeholder=" "
              innerRef={register(PriceOptions.document_link)}
              onBlur={() => {
                let versionl = document.getElementById("link_document_api");
                if (versionl && versionl.value) {
                  versionl.value = versionl.value.trim();
                }
              }}
              className={classNames({
                "is-invalid": errors["link_document_api"],
              })}
              defaultValue={discountData && discountData.link_document_api}
            />
            <small className="text-danger">
              {errors?.link_document_api && errors.link_document_api.message}
            </small>
          </FormGroup>

          <div style={{ textAlign: "end" }}>
            <Button
              type="submit"
              className="mr-1"
              color="primary"
              disabled={disable}
            >
              <FormattedMessage id="update" />
            </Button>
            <Button
              type="reset"
              color="secondary"
              outline
              onClick={() => history.goBack()}
            >
              <FormattedMessage id="Cancel" />
            </Button>
          </div>
        </Form>
      </Col>
    </Row>
  );
};
export default injectIntl(DocumentTab);
