import api from "../../../../../../constants/api";
import Service from "../../../../../../services/request";

export const getData = (params) => {
  return async (dispatch) => {
    await Service.send({
      method: api.LIST_PRODUCT_DOCUMENT_LINK.method,
      path: api.LIST_PRODUCT_DOCUMENT_LINK.path,
      data: params,
    }).then((res) => {
      dispatch({
        type: "GET_DATA_DOCUMENT_PRODUCT",
        data: res?.data,
        params,
      });
    });
  };
};

export const getById = (data) => {
  return async (dispatch) => {
    await Service.send({
      method: api.GET_DOCUMENT_LINK.method,
      path: api.GET_DOCUMENT_LINK.path,
      query: data,
    })
      .then((response) => {
        dispatch({
          type: "GET_DOCUMENT_LINK_PRODUCT",
          selected: response?.data,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  };
};

export const add = (staff) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.CREATE_PRODUCT_DOCUMENT_LINK.method,
      path: api.CREATE_PRODUCT_DOCUMENT_LINK.path,
      data: staff,
    })
      .then((response) => {
        dispatch({
          type: "ADD_DOCUMENT_LINK_PRODUCT",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "ADD_DOCUMENT_LINK_PRODUCT",
          err,
        })
      );
  };
};

export const update = (staff) => {
  return async (dispatch, getState) => {
    await Service.send({
      method: api.UPDATE_PRODUCT_DOCUMENT_LINK.method,
      path: api.UPDATE_PRODUCT_DOCUMENT_LINK.path,
      data: staff,
    })
      .then((response) => {
        dispatch({
          type: "UPDATE_DOCUMENT_LINK_PRODUCT",
          response,
        });
      })

      .catch((err) =>
        dispatch({
          type: "UPDATE_DOCUMENT_LINK_PRODUCT",
          err,
        })
      );
  };
};
export const remove = (staff) => {
  return async (dispatch) => {
    await Service.send({
      method: api.DELETE_PRODUCT_DOCUMENT_LINK.method,
      path: api.DELETE_PRODUCT_DOCUMENT_LINK.path,
      data: staff,
    })
      .then((response) => {
        dispatch({
          type: "DELETE_DOCUMENT_LINK_PRODUCT",
          response,
        });
      })
      .catch((err) => console.log(err));
  };
};
